  mutations: {// /        React.  .  ,   .  .
    setOpen(state, id){
      state.droplist = [...state.droplist.map(item => {
        if (item.id === id) {
          return {...item, open: !item.open}
        } else if (item.body){
          return {
            ...item,
            body: [...item.body.map(item=>{
              if (item.id === id) {
                return {...item, open: !item.open}
              } else if (item.body){
                return {
                  ...item,
                  body: [...item.body.map(item=>{
                    if (item.id === id) {
                      return {...item, open: !item.open}
                    } else return item
                  })]
                }
              }
              return item
            })]
          }
        }
        return item
      })]
    },
  },
