import Vue from "vue";
import VueRouter from "vue-router";
import firebase from 'firebase/compat';

Vue.use(VueRouter);

const routes = [//  
  {
    path: '/login',
    name: 'login',
    meta: {layout: 'login'},
    component: () => import('../views/loginViews/Auth.vue')
  },
  {
    path: '/',
    name: 'main',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Main.vue')
  },
  {
    path: '/alarm',
    name: 'alarm',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Alarm.vue')
  },
  {
    path: '/potential',
    name: 'potential',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Potential.vue')
  },
  {
    path: '/notes',
    name: 'notes',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Notes.vue')
  },
  {
    path: '/history',
    name: 'history',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/History.vue')
  },
  {
    path: '/fund',
    name: 'fund',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Fund.vue')
  },
  {
    path: '/raredata',
    name: 'raredata',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/RareData.vue')
  },
  {
    path: '/weight',
    name: 'weight',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Weight.vue')
  },
  {
    path: '/lab',
    name: 'lab',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Lab.vue')
  },
  {
    path: '/hyp',
    name: 'hyp',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Hyp.vue')
  },
  {
    path: '/reserve',
    name: 'reserve',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Reserve.vue')
  },
  {
    path: '/trash',
    name: 'trash',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/mainViews/Trash.vue')
  },
  {
    path: '/cosmo',
    name: 'cosmo',
    meta: {layout: 'main', auth:true},
    component: () => import('../views/emptyViews/Cosmo.vue')
  },
  {
    path: '/resume',
    name: 'resume',
    meta: {layout: 'resume'},
    component: () => import('../views/resumeViews/Resume.vue')
  },
  {
    path: '/beloe',
    name: 'beloe',
    meta: {layout: 'beloe'},
    component: () => import('../views/beloeViews/Beloe.vue')
  },
  {
    path: '/*',
    name: 'notFound',
    meta: {layout: 'empty'},
    component: () => import('../views/emptyViews/NotFound.vue')
  },
];

const router = new VueRouter({mode: 'history', routes});

// .     
router.beforeEach((to,from,next)=>{
  const currentUser = firebase.auth().currentUser//      
  const isNeedAuth = to.matched.some(item=>item.meta.auth)//   auth   ?
  // console.log('router currentUser',currentUser.uid)
  // console.log('router need auth?:',isNeedAuth)
  if(isNeedAuth &&!currentUser){
    console.log('router move to login')
    next('/login')//   
    // next('/login?message=login')//   
  }else{
    next()// 
  }
})

export default router;
